import '@babel/runtime-corejs3/core-js-stable/instance/index-of';

import { detection as detect } from '@accedo/xdk-core';

const isFireTV = () => {
    const ua = navigator.userAgent.toLowerCase();
    const test = ua.indexOf('aft') !== -1;
    if (test) {
        // eslint-disable-next-line no-console
        console.log('XDK Device detection successful, initializing Amazon Fire TV device abstraction.');
    }
    return ua.indexOf('aft') !== -1;
};

const detection = detect(isFireTV);

export default detection;
